@font-face {
  font-family: bebas_neue_bold;
  src: url("../../../assets/fonts/bebas-neue-bold.otf");
}

@font-face {
  font-family: bellaboo;
  src: url("../../../assets/fonts/BELLABOO-Regular.ttf");
}

@font-face {
  font-family: bebas_neue__book;
  /* src: url("../../../assets/fonts/BebasNeue_Book.ttf"); */
