}

/* titulos */
h2 {
  text-align: center;
  font-family: "bellaboo";
  /* font-family: "bebas_neue_bold"; */
}

/* subtitulos */
h4 {
  font-family: "bebas_neue_bold";
}

h5 {
  font-family: "bellaboo";
}

/* corpo do texto */
p {
  font-family: bebas_neue__book;
  font-size: 20px;
}

.text-center {
  text-align: center !important;
}

.flex-center {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}

.home-adm {
  background-color: #152740;
}
.title-perfil-aluno{
  margin-left: 24px;
  margin-right: 24px;
}
.list-dados-aluno{
  margin-left: 10%;
  margin-top: 5%;
  margin-right: 10%;
  display: flex;
  border: 1px solid rgb(43, 43, 43);
  border-radius: 20px;
  box-shadow: 1px 1px rgb(32, 32, 32);
  padding: 2%;
  background-color: white;
}
.item-dados-aluno{
  margin-left: 10%;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}
.container-info-aluno{
